#header-intersection-observer {
	position: absolute;
	top: 100vh;
	height:calc(100% - 100vh);
	width: 10px;
	display: block;
}

body.scrolled{
	#masthead{
		background-color: #fff;;
		box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
		height: 7rem;
		.site-branding {
			a {
				height: 45px;
			}
		}
		#site-navigation.toggled {
			@include media-screen($bk_mobile){
				position: absolute;
				left: 0;
				right: 0;
				top: 7rem;
				background-color: $color_block_palet6;
			}
		}
	}
	.header-spacer{
		height: 7rem;
	}
}
.header-spacer{
	height: 10rem;
	transition: height 0.25s;
}
#masthead{
    background-color: transparent;
    position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 100;
	padding: 1.2rem 2.2rem;
	transition: background-color 0.25s, height 0.25s, box-shadow 0.25s;
	height: 10rem;
	box-shadow: 0px 5px 10px rgba(0, 0, 0, 0);

    ///
    /// @at-root pour si .admin-bar est présent, on décalle le fixed du sticky pour ne pas superposer le header et le menu wp.
    ///
    @at-root .admin-bar &{
        top: 32px;
    }

    @include media-screen(782px){
        @at-root .admin-bar &{
            top: 46px;
        }
    }

    @include media-screen(600px){
        @at-root .admin-bar &{
            top: 0px;
        }
	}

	.menu-toggle{
		border:none;
		padding:0;
		font-size: 3.5rem;
		background: none;
		@include media-screen($bk_mobile){
			margin-left:auto;
		}
	}

	
	.site-branding {
		display: flex;
		a {
			max-width: 115px;
			align-self: center;
			display: inline-block;
			height: 75px;
			transition: height 0.25s;
			img {
				height: 100%;
			}
		}
	}
	.nav-wrapper {
		display: flex;
		#site-navigation {
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			
				@include media-screen($bk_mobile){
					
					position: absolute;
					left: 0;
					right: 0;
					top: 10rem;
					background-color: $color_block_palet6;
					flex-direction: column-reverse;
					flex-grow: 1;
				}
			

			.additional-options{
				@include media-screen($bk_mobile){
					border-bottom: 1px solid #000;

					width: 100%;
				}
				ul{
					@include media-screen($bk_mobile){
						display: flex;
						justify-content: space-around;
					}
					li{
						a{
							font-size: 1.4rem;
							@include media-screen($bk_mobile){
								font-size: 1.6rem;
							}
						}
					}
				}
			}
			&>div{
				align-self: flex-end;
				@include media-screen($bk_mobile){
					width: 100%;
					align-self: flex-start;
				}
				&:not(:first-child){
					margin: auto 0;
				}
				ul{
					@include media-screen($bk_mobile){
						border-top: 1px solid #000;
					}
					li {
						box-sizing: border-box;
						margin-right: 3rem;
						&:last-child {
							margin-right : 0;
						}
						&.menu-item-has-children {
							&>a:after{
								content : "\f107";
								font-size: 1.6rem;
								font-family: "Font Awesome 5 Pro";
								padding-left:0.2rem;
								vertical-align: text-top;
							}
						}
						a{
							font-size: 1.6rem;
							@include media-screen($bk_mobile){
								padding: 0.4rem;
								font-size: 2rem
							}
							img{
								height: 1.6rem;
								vertical-align: text-bottom;
								width: 1.6rem;
								transition: filter 0.25s;
							}
							&:hover{
								img{
									filter: invert(8%) sepia(96%) saturate(3712%) hue-rotate(339deg) brightness(143%) contrast(141%);
								}
							}
						}
					}
				}
				.sub-menu-wrap{
					padding-top:8px;
					transform: translateX(-50%);

					ul.sub-menu{
						position: relative;
						display: flex;
						background-color: #fff;
						padding-top: 1em;
						padding:1.1rem 1.1rem;
						border-radius: 0.5rem;
						flex-direction: column;
						filter: 
							drop-shadow(0px 1px 0 #000)
							drop-shadow(0px -1px 0 #000)
							drop-shadow(1px 0px 0 #000)
							drop-shadow(-1px 0px 0 #000)
						;
						li{
							text-align: center;
							margin-right: 0;

							a{
								width: inherit;
							}
						}
						&::before {
							content:"";
							width: 0;
							position: absolute;
							top:-8px;
							height: 0;
							border-style: solid;
							border-width: 0 9px 8px 9px;
							border-color: transparent transparent #fff transparent;
							left: 50%;
							transform: translateX(-50%);

						}
					}
				}
				
			}

		}
	}
}
