a {
	color: $color__link;
	transition: border-color 0.5s , background-color 0.25s, color 0.25s;
	&:visited {
		color: $color__link-visited;
	}
	&:hover,
	&:focus,
	&:active {
		color: $color__link-hover;
	}
	&:focus {
		outline: thin dotted;
	}
	&:hover,
	&:active {
		outline: 0;
	}
}
a.wp-block-button__link {
	padding:0.2rem 0.8rem;
	font-weight: 600;
}



@each $index, $hex in $colors {
	div:not(.is-style-outline) a.wp-block-button__link.has-color-#{$index}-background-color {
		border:  2px solid $hex;
		color:  #fff;
		background-color: $hex;
		&:hover{
			background-color:   $hex;
			border:   2px solid $hex;
			background-color:transparent;
			color:$hex;
		}
	}
}

@each $index, $hex in $colors {
	div.is-style-outline a.wp-block-button__link.has-color-#{$index}-color {
		border-color:$hex;
		background-color: transparent;
		color:$hex;
		transition: color 0.25s,background-color 0.25s;
		&:hover{
			background-color: $hex;
			border-color: $hex;
			color:#fff;
		}
	}
}


