.site-footer{
	margin-top: 0px;
	background-color: #000;
	padding: 3rem;;

	.footer-contact {
		display: flex;
		@include media-screen($bk-mobile){
			flex-direction: column;
		}
		.site-branding{
			margin-bottom:0;
			img{
				max-width: 110px;
			}
		}

		&>div{
			display: flex;
			flex-direction: column;
			
			&.moitiee{
				width: 50%;
				@include media-screen($bk-mobile){
					width: 100%;
				}
			}
			&.quart{
				width: 35%;
				margin-left: auto;
				@include media-screen($bk-mobile){
					width: 100%;
				}
			}
			p.label{
				color:#fff;
				font-weight: 600;
				font-size: 2.2rem;
				margin-bottom:1rem;
				border-bottom: 1px solid #ffffff;
				&.no-border{
					border-bottom:none;
				}
			}

			&:first-of-type>:first-of-type {
				width: 75%;
			}
			&>div{
				margin-bottom:4rem;
				&.align-bottom{
					margin-top: auto;
				}
			}
			ul{
				margin:0;
				padding:0;
				display: flex;
				list-style-type: none;
				li{
					margin-right:4rem;
					&.menu-item-has-children {
						&>a:after{
							content : "\f107";
							font-size: 1.6rem;
							font-family: "Font Awesome 5 Pro";
							padding-left:0.2rem;
							vertical-align: text-top;
						}
						&:hover{
							.sub-menu-wrap{
								display: block;
							}
						}
					}

					p{
						margin: 1rem 0;
					}
					a {
						color:#fff;
						text-decoration: none;
						
						&:hover {
							color: $color__link-hover;
						}
					}
					form {
						display: flex;
						border:1px solid #fff;
						border-radius: 0.3rem;
						input{
							background-color: transparent;
							color:#ffffff;
							border:none;
							border-radius: 0;
							transition: background-color 0.25s, color 0.25s;
							&::placeholder {
								color:#ffffff;
							}
							&:last-child {
								border-left: 1px solid #fff;
							}
							&[type="submit"]:hover {
								background-color: #fff;
								color: #000;
							}
						}
						
					}
					.sub-menu-wrap{
						padding-top:8px;
						display: none;;
	
						ul.sub-menu{
							position: relative;
							display: flex;
							background-color: #000;
							padding-top: 1em;
							padding:1.1rem 1.1rem;
							border-radius: 0.5rem;
							flex-direction: column;
							filter: 
								drop-shadow(0px 1px 0 #fff)
								drop-shadow(0px -1px 0 #fff)
								drop-shadow(1px 0px 0 #fff)
								drop-shadow(-1px 0px 0 #fff)
							;
							li{
								text-align: center;
								margin-right: 0;
								p {
									line-height: 1.1;
									
								}
								a{
									width: inherit;
								}
							}
							&::before {
								content:"";
								width: 0;
								position: absolute;
								top:-8px;
								height: 0;
								border-style: solid;
								border-width: 0 9px 8px 9px;
								border-color: transparent transparent #000 transparent;
								left: 50%;
								transform: translateX(-50%);
	
							}
						}
					}
				}
			}
		}

	}
	.site-info {
		display: flex;
		border-top: 1px solid #b3aead;
		padding-top: 1.5rem;
		@include media-screen($bk-mobile){
			flex-direction: column;
			justify-content: center;
		}
		a{
			color: #b3aead;
			text-decoration:none;
			margin-right:4rem;
			font-size: 1.4rem;
			&:hover{
				color:$color__link-hover;
			}
			@include media-screen($bk-mobile){
				align-self:center;
				margin-bottom:1rem;
			}
		}
		span {
			margin-left:auto;
			color: #b3aead;
			@include media-screen($bk-mobile){
				margin-left:inherit;
				align-self:center;
			}
		}
	}
}