.main-navigation {
	clear: both;
	display: block;
	width: 100%;

	ul {
		display: flex;
		list-style: none;
		margin: 0;
		padding-left: 0;

		li {
			position: relative;
	
			/// @todo 
			&:hover > a,
			&.focus > a {
			}
		}

		/// @todo
		.current_page_item > a,
		.current-menu-item > a,
		.current_page_ancestor > a,
		.current-menu-ancestor > a {
		}
	
		a {
			display: block;
			text-decoration: none;
		}

		/// @todo
		:hover > a,
		.focus > a,
		a:hover,
		a.focus {
		}

		.sub-menu-wrap {
			position: absolute;
			top: 100%;
			left: -999em;
			z-index: 99999;

			// ul {
			// 	left: -999em;
			// 	top: 0;
			// }

			li {
				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}
			}

			a {
				width: 200px;
			}
		}

		li:hover > .sub-menu-wrap,
		li.focus > .sub-menu-wrap {
			left: 50%;
		}
	}
}

/* Small menu. */
.menu-toggle {
	display: none;
}

@include media-screen($bk_mobile) {
	.menu-toggle,
	.main-navigation.toggled ul , 
	.main-navigation.toggled .additional-options {
		display: block;
		
	}
	.main-navigation ul,
	.main-navigation .additional-options{
		display: none;
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {

	.site-main & {
		margin: 0 0 1.5em;
		overflow: hidden;
	}

	.nav-previous {
		float: left;
		width: 50%;
	}

	.nav-next {
		float: right;
		text-align: right;
		width: 50%;
	}
}
