
.banner-top {
	padding:3rem;
	padding-top: 13rem;
	padding-bottom: 5rem;
	background-color: $color_block_palet6 ;
	@include media-screen($bk_mobile){
		padding-left:1.5rem;
		padding-right:1.5rem;

	}

	span.label-new + h1 {
		margin-top: 0;
	}
	span.label-new{
		color:$color_block_palet3;
		display: block;
		margin-bottom: -0.2rem;
		font-weight: 600;
		font-size: 1.4rem;
		padding-bottom: 0;
		line-height: 1;
	}

}
.wrap-extra{
	padding: 0 3rem;
	@include media-screen($bk_mobile){
		padding-left:1.5rem;
		padding-right:1.5rem;
	}
}

.content-banner>div{
		width: 50%;
		@include media-screen($bk_mobile){
			width: 100%;
		}
	}
	
	

.banner-extra, .content-banner {
	display: flex;
	flex-wrap: wrap;
	&>div{
		width: 50%;
		@include media-screen($bk_mobile){
			width: 100%;
		}
		.search_box{
			background-color: #ffffff;
			border: 1px solid #000;
			padding:3rem 3rem;
			padding-bottom: 4rem;
			@include media-screen($bk_mobile,min){
				margin-top: -9rem;
			}
			.form-wrapper {
				padding-top:1.5rem;
				width: 100%;
				display: flex;
				
				input,button{
					background-color: #ffffff;
					border:1px solid #000;
					border-radius: 0;
					font-size: 1.5rem;
					color:#000;
					padding: 1rem 1.75rem;
					transition: color 0.25s, background-color 0.25s;;
				}
				button{
					&:hover{
						color:#ffffff;
						background-color: #000000;
					}
				}
				input{
					flex-grow: 1;
					&::placeholder{
						color:#000;
					}
					border-right:none;
				}
			}
			.finder-tip {
				margin: 2rem 0;
				display: flex;
				.circle{
					display: flex;
				
					align-self: flex-start;
					height: 4.5rem;
					width: 4.5rem;
					justify-content: center;
					line-height: 4.5rem;
					font-size: 3rem;
					vertical-align: sub;
					border: 1px solid #000;
					border-radius: 50%;
					span {
						align-self: center;
						font-weight: 300;
					}
				}
				&>span:last-child{
					align-self: center;
					font-size: 1.4rem;
					margin-left: 2rem;
					a{
						color: $color_block_palet3;
						&:hover {
							color:#000000;
						}
					}
				}
			}
		}
	}
}